.profile-info__job {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    color: #fff;
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
    margin: 0;
    max-width: 580px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    align-self: center;
  }
  
  @media screen and (max-width: 896px) {
    .profile-info__job {
      max-width: 294px;
      grid-column-start: auto;
      grid-column-end: auto;
      grid-row-start: auto;
      grid-row-end: auto;
    }
  }
  
  @media screen and (max-width: 597px) {
    .profile-info__job {
      max-width: 221px;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      text-align: center;
    }
  }